import React from 'react';
import {Link} from 'react-router-dom';
import { Button, Jumbotron, Container, Row, Col } from "reactstrap";
import Header from '../../header.js';

const Po = () =>{
    return(
        <Container fluid className="App">
        <Header/>
        <Container className="">
            <Jumbotron>
                <h1>Pesquisa Operacional</h1>
                <Col xs={12} md={12} className="mx-auto my-5 text-left">
                    <h4 class="text-center mt-10">PESQUISA OPERACIONAL</h4>
                    <p>A PO (Pesquisa operacional) representa um problema 
                        da vida real em um modelo matemático que pode ser 
                        resolvido matematicamente com técnicas da PL 
                        (Programação Linear) e a solução matemática resolve 
                        o problema com a melhor solução possível, respeitando 
                        as restrições e aproveitando o máximo dos recursos. O 
                        seu nome indica que realizamos uma pesquisa de operações, 
                        onde se entende, por operações, as atividades das 
                        organizações executadas por homens e máquinas.</p>

                    <h4 class="text-center"> Surgimento da Pesquisa Operacional</h4>
                    <p>A PO surgiu durante a segunda guerra mundial (1939-1945) da 
                        necessidade militar de solucionar problemas relacionados a 
                        estratégias logístico-táticas tais como: melhor distribuição 
                        de radares, dimensionamento de frotas, alimentação das tropas, 
                        manutenção e inspeção de aviões entre outras atividades. Mas 
                        ainda eram princípios muitos básicos e com apenas essa 
                        aplicabilidade de cunho militar, com o passar dos anos 
                        foi-se desenvolvendo mais e mais e abrangendo mais áreas, 
                        resolvendo seus problemas.Entre os séculos XVIII e XIX, Período 
                        da revolução Industrial, ocorreu um grande crescimento no tamanho 
                        e na complexidade das organizações. O trabalho teve que ser dividido 
                        e as responsabilidades gerenciais das empresas separadas, gerando 
                        mais especializações as quais também trouxeram mais novos problemas. 
                        Com todo esse crescimento ficou mais difícil alocar os recursos 
                        disponíveis para as diversas atividades de maneira eficiente para 
                        toda a organização. Para solucionar esses novos problemas a PO foi 
                        mais bem definida, a qual traz o melhor caminho para resolver o 
                        problema em questão.Em campo, pode-se dizer que a primeira 
                        experiência industrial da PO foi com Frederick W. Taylor, pelos 
                        meados de 1885, que aplicou as primeiras técnicas de análise 
                        cientificas ao sistema de produção. Em uma siderúrgica ele analisou 
                        o trabalho dos carregadores de vagões de trem com carvão. Dizia-se 
                        que o trabalhador com a pá maior era o mais eficiente. Taylor, com 
                        seu estudo, analisou que o mais eficiente seria o trabalhador que 
                        usasse a menor pá, além de diminuir o cansaço do mesmo. (FALCÃO, 1999).</p>
                </Col>
                <p class="text-right">Fonte: Maranhão, Reginaldo. doGraphics – Ferramenta de ensino web para
                     resolução gráfica em pesquisa operacional. 2019. 14 f. Trabalho de conclusão
                     de curso (Graduação) - Centro Universitário Farias Brito, Fortaleza-CE, 2019.</p>
            </Jumbotron>
        </Container>
    </Container>
  )
};

export default Po;