import React, {useState} from 'react';
import {Link} from 'react-router-dom';
import { 
    Jumbotron, 
    Container, 
    Row, 
    Col,
    Button
} from "reactstrap";
import logoFbuni from './logoFbuni.png';
import logoDG from './logoSite.png';
import Header from '../header.js'

const Inicio = () =>{
    const [dropdownOpen, setDropdownOpen] = useState(false);

    return(
        <Container fluid className="App">
        <Header/>
        <Row className="Linha">
            <Col xs={12} md={10} className="mx-auto my-4">
                <Jumbotron className="Jumbo">
                    <Row>
                        <Col className="Logos" lg={6}>
                            <img className="LogoDg mx-auto" src={logoDG} alt="Logo"/>
                        </Col>
                        
                        <Col className="Logos" lg={6}>
                            <img className="LogoFb mx-auto" src={logoFbuni} alt="Logo" />
                        </Col>
                    </Row>
                    <Row>
                        <h2 className="mx-auto">Bem vindo ao doGraphics!</h2>
                    </Row>
                    <Row className="mt-12">
                        <p>Esta é a ferramenta DoGraphics, ela foi criada por mim, Reginaldo
                            Maranhão, estudande de Ciência da computação do Centro Univeritário 
                            Farias Brito em  conjunto com o meu professor orientador Maikol 
                            Magalhães. Na cadeira de Pesquisa Operacional, precisamente no 
                            conteúdo sobre problemas de programação linear, onde eu como aluno
                            e o Maikol como professor, sentimos a necessidade de uma boa ferramenta
                            que resolvesse problemas de PO, usando o método de resolução gráfica. 
                            Utilizamos algumas ferramentas, mas elas pecavam em algumas pontualidades,
                            desde questões de layout e usabilidade, até incorência e/ou confusão
                            das respostadas dadas pelas ferramentas usadas.
                        </p>

                        <p>Com isso, eu procurei o professor Maikol para realizamos meu TCC em cima
                            disso, criar uma boa ferramenta para ser utilizada dentro das saulas de
                            aulas, para os alunos e professores terem a sua disposição uma boa ferramenta
                            para resolver problemas de PO de forma clara, rápida e o mais fácil para alunos
                            e professores. Espero que de alguma forma possa ajudar as futuras gerações
                            da ciência da computação e engenharia nas resoluções de problemas de PL 
                            em suas aulas, e que professores possa ter uma boa ferramenta para auxiliar
                            melhor seus alunos.
                        </p>
                    </Row>

                    
                <Button href="/LinearProgramation/SinglePage" color="success">Resolver Problemas</Button>{' '}
                </Jumbotron>
            </Col>
        </Row>
    </Container>
  )
};

export default Inicio;