import React from 'react';
import {Link} from 'react-router-dom';
import { Button, Jumbotron, Container, Row, Col } from "reactstrap";
import Header from '../../header.js';
import Ex from '../ex.jpg';
import Ex2 from '../ex2.jpg';
import Ex3 from '../ex3.jpg';
import Ex4 from '../ex4.jpg';
import Ex5 from '../ex5.jpg';
import Ex6 from '../ex6.jpg';

const MetodoGrafico = () =>{
    return(
<Container fluid className="App">
        <Header/>
        <Container className="">
            <Jumbotron>
                <h1>Método gráfico</h1>
                <Col xs={12} md={12} className="mx-auto my-5 text-left">
                    <h4 class="text-center mt-10">MÉTODO DE RESOLUÇÃO GRÁFICA</h4>
                    <p>Visto que, problemas de PL com duas (ou três variáveis) podem 
                        ser plotados em um gráfico bidimensional, podemos encontrar 
                        o conjunto de soluções possíveis para esse problema utilizando 
                        o método de resolução gráfica. O método é composto de um 
                        passo-a-passo até conseguir encontrar a solução desejada. 
                        Praticamente, o método consiste em desenhar um gráfico 
                        bidimensional de modo que possamos visualizar a área com 
                        as possíveis soluções. Mas para que o processo funcione, o 
                        problema precisa estar disposto no formato de um PPL, como 
                        já foi mostrado.</p>


                    <p>Pode-se explicar bem o processo com um pequeno exemplo:</p>

                    <img className="mx-auto" src={Ex} alt="Ex" />

                    <p>Com o problema já modelado, as variáveis de decisão já identificadas 
                        (x_1,x_2) e assumindo que ambas são maiores que 0, logo, trabalhamos 
                        no gráfico apenas na região positiva, podemos iniciar a resolver o 
                        problema. Iniciamos transformando as inequações das restrições em 
                        equações apenas mudando os sinais de desigualdade para o sinal de igualdade:</p>

                    <img className="mx-auto" src={Ex2} alt="Ex2" />

                    <p>Agora com as equações normalizadas, podemos desenhar as retas no gráfico. 
                        Desenhando uma a uma vamos delimitando a área, até encontrar a solução 
                        factível que é considerada melhor. Podemos observar na Figura 1, a região 
                        factível delimitada pela primeira reta: x_1=60, passando pelo ponto “A”.</p>
                    

                    <img className="mx-auto" src={Ex3} alt="Ex3" />

                    <p>A área colorida em verde, representa as possíveis soluções para o problema 
                        depois de traçada apenas a primeira restrição. Dentro dessa área existe 
                        um par ordenado (x_1,x_2) que resolve o problema com a melhor solução 
                        possível. De acordo que as outras retas que representam as restrições 
                        forem traçadas a área vai se delimitando mais ainda, tornado mais evidente 
                        as possíveis soluções e facilitando encontrar a melhor solução. Adicionando 
                        a reta x_2=50, temos a Figura 2. A área fica delimitada pelos pontos B-C. 
                        A reta F-G, representa a equação  x_1+2x_2=120 mostrada na Figura 3, 
                        delimitando ainda mais a área de soluções factíveis. Traçando a reta da 
                        última restrição  x_1+ x_2=20, obtemos o gráfico representado na Figura 4.
                        Traçadas todas as retas, temos a região delimitada nos fornecendo o conjunto 
                        de soluções factíveis. A melhor solução se encontra nos vértices da área 
                        colorida, nesse caso nos vértices A, C, D, E, H ou I, basta testar os valores 
                        de cada vértice na função objetivo e observar qual maximiza a função, já que 
                        o objetivo da função objetivo é maximizar. Fazendo isso, encontramos que a 
                        solução ótima do problema se encontra no vértice C (60, 30).</p>

                        <img className="mx-auto" src={Ex4} alt="Ex4" />
                        <img className="mx-auto" src={Ex5} alt="Ex5" />
                        <img className="mx-auto" src={Ex6} alt="Ex6" />

                    <p>A Figura 5 nos mostra que a solução ótima se encontra no vértice C (60, 30), 
                        isso quer dizer que se substituirmos x1 por 60 e x2 por 30, vamos conseguir 
                        maximizar a função correspondente a função objetivo respeitando todas as restrições.</p>
                </Col>
                <p class="text-right">Fonte: Maranhão, Reginaldo. doGraphics – Ferramenta de ensino web para
                     resolução gráfica em pesquisa operacional. 2019. 14 f. Trabalho de conclusão
                     de curso (Graduação) - Centro Universitário Farias Brito, Fortaleza-CE, 2019.</p>
            </Jumbotron>
        </Container>
    </Container>
  )
};

export default MetodoGrafico;